import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import App from "./App";
import { ROUTE_PET_NUMBER, ROUTE_PET_STAMP } from "./constants";
import PetStamp from "./sites/pet_stamp/PetStamp";

function Routes() {
  return (
    <Router>
      <Switch>
        <Route path="/" exact>
          <App></App>
        </Route>
        <Route path={ROUTE_PET_STAMP}>
          <PetStamp></PetStamp>
        </Route>
        <Route path={ROUTE_PET_NUMBER}>
          <App></App>
        </Route>
      </Switch>
    </Router>
  );
}

export default Routes;
